import React,{ useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import { Form, Button, Row, Col} from 'react-bootstrap';
import { useDispatch, useSelector } from 'react-redux';
import Message from '../components/Message';
import Loader from '../components/Loader';
import { login } from '../actions/userActions.js';
import FormContainer  from '../components/FormContainer.js'

const LoginScreen = ({ history,location }) => {

  const [ email, setEmail ] = useState('');
  const [ password, setPassword ] = useState('');

  const dispatch = useDispatch();

  const userLogin  = useSelector(state => state.userLogin)

  const { loading, error, userInfo } = userLogin;

  const redirect = location.search ? location.search.split('=')[1] : '/';

  useEffect (() => {
    if(userInfo) {
      history.push(redirect)
    }
  },[history, userInfo, redirect])

  const submitHandler = (e) =>{
    e.preventDefault();
    //Dispatch Login
    dispatch(login( email, password ));
  }

  return (
    <FormContainer>
      <br/>
      <h4 className="p-2 bg-light text-dark border rounded">Sign In</h4>
      <br/>
      { error && <Message variant='danger'>{error}</Message>}
      { loading && <Loader />}
      <Form onSubmit={submitHandler}>
        <Form.Group controlId='email'>
          <Form.Label>Email Address</Form.Label>
          <Form.Control type='email' placeholder='Enter Email' value={email} onChange={(e) => setEmail(e.target.value)}></Form.Control>
        </Form.Group>
        <Form.Group controlId='password'>
          <Form.Label>Password</Form.Label>
          <Form.Control type='password' placeholder='Enter Passwrod' value={password} onChange={(e) => setPassword(e.target.value)}></Form.Control>
        </Form.Group>
        <Button type='submit' variant='dark'> Sign In</Button>
        <Row className='py-3'>
          <Col>
            New Customer? <Link to={redirect ? `/register?redirect=${redirect}` : '/register'}>Register</Link>
          </Col>
        </Row>
      </Form>
    </FormContainer>
  )
}

export default LoginScreen
