import React,{ useEffect } from 'react';
import { LinkContainer } from 'react-router-bootstrap';
import { Table, Button} from 'react-bootstrap';
import { useDispatch, useSelector } from 'react-redux';
import Message from '../components/Message';
import Loader from '../components/Loader';
import { listOrders } from '../actions/orderActions.js';

const OrderListScreen = ({ history }) => {

  const dispatch = useDispatch();
  //Get orderList from store.js 
  const orderList = useSelector(state => state.orderList);
  const { loading, error, orders } = orderList;
  
  const userLogin = useSelector(state => state.userLogin);
  const { userInfo } = userLogin;

  useEffect(() => {
    if(userInfo && userInfo.isAdmin) {
      dispatch(listOrders())
    } else {
      history.push('/login')
    }
  }, [dispatch, history, userInfo])

  return (
    <>
     {/* <h4 className="p-2 bg-light text-dark border rounded text-center">Users list</h4><br/> */}
     <h4 className="font-weight-bold text-uppercase">ORDERS</h4> 
     {loading ? <Loader /> :error ? ( <Message variant='danger'>{error}</Message>) : (
       <Table striped bordered hover responsive className="table-sm text-center">
        <thead>
          <tr>
              <th>ID</th>
              <th>USER</th>
              <th>DATE</th>
              <th>TOTAL</th>
              <th>PAID</th>
              <th>DELIVERED</th>
              <th></th>
          </tr>
        </thead>
        <tbody>
          {/* Loop throught the orders what we got from state */}
          {orders.map(order => (
            <tr key={order._id}>
              <td>{order._id}</td>
              <td>{order.user && order.user.name}</td>
              <td>{order.createdAt.substring(0, 10)}</td>
              <td>${order.totalPrice}</td>
              
              <td>{order.isPaid ? (order.paidAt.substring(0, 10)) : (<i className='fas fa-times' style={{color: 'red'}}></i>) }</td>
             
              <td>{order.isDelivered ? (order.deliveredAt.substring(0, 10)) : (<i className='fas fa-times' style={{color: 'red'}}></i>) }</td>

              <td>
                <LinkContainer to={`/order/${order._id}`}>
                  <Button variant='light' className='bg-white btn-sm mr-2'> Details </Button>
                </LinkContainer>
               
              </td>
            </tr>
          ))}
        </tbody>
       </Table>
     )}
    </>
  )
}

export default OrderListScreen
