import React,{useState, useEffect} from 'react';
import { Link } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux'
import { Row, Col, Image, ListGroup, Card, Button, Form} from 'react-bootstrap';
import Rating from '../components/Rating';
import Message from '../components/Message';
import Loader from '../components/Loader';
import Meta from '../components/Meta';
import { listProductDetails, createProductReview } from '../actions/productActions';
import { PRODUCT_CREATE_REVIEW_RESET} from '../constants/productConstants'

const ProductScreen = ({ history, match }) => {

  const [ qty, setQty ] = useState(1);
  const [ rating, setRating ] = useState(0);
  const [ comment, setComment ] = useState('');

  const dispatch = useDispatch();

  const productDetails = useSelector((state) => state.productDetails);
  const { loading, error, product } = productDetails;
  
  const productReviewCreate = useSelector((state) => state.productReviewCreate);
  const { success:successProductReview, loading: loadingProductReview, error:errorProductReview } = productReviewCreate;

  const userLogin = useSelector(state => state.userLogin);
  const { userInfo } = userLogin;

  useEffect(() => {
    if(successProductReview){
      alert('Review Submitted');
      setRating(0);
      setComment('');
    }
    if (!product._id || product._id !== match.params.id) {
      dispatch(listProductDetails(match.params.id))
      dispatch({ type: PRODUCT_CREATE_REVIEW_RESET })
    }
  },[dispatch, match, successProductReview])

  const addToCartHandler = () => {
      history.push(`/cart/${match.params.id}?qty=${qty}`);
  }
  
  const submitHandler = (e) => {
      e.preventDefault();
      dispatch(createProductReview(match.params.id, { rating, comment }));
  }


  return (
    <>      
      <Link className="btn btn-light my-3" to="/">
        <i className="fas fa-chevron-left pr-2"></i> Go Back
      </Link>
      {loading ? <Loader /> : error ? <Message variant='danger'>{error}</Message> : (
        <>
        <Meta title={product.name}/>
        <Row>
          <Col md={6}>
            <Image src={product.image} alt={product.name} fluid/>
          </Col>
          <Col md={3}>
            <ListGroup variant="flush">
              <ListGroup.Item>
                <h3> {product.name} </h3>
              </ListGroup.Item>
              <ListGroup.Item>
                <Rating value={product.rating} text={`${product.numReviews} reviews`}/>
              </ListGroup.Item>
              <ListGroup.Item>
                Price: ${product.price} 
              </ListGroup.Item>
              <ListGroup.Item>
                Description: {product.description} 
              </ListGroup.Item>
            </ListGroup>
          </Col>
          <Col md={3}>
            <Card>
              <ListGroup variant="flush">
                <ListGroup.Item>
                  <Row>
                    <Col>
                      Price
                    </Col>
                    <Col>
                      <strong> ${product.price} </strong>
                    </Col>
                  </Row>
                </ListGroup.Item>
                <ListGroup.Item>
                  <Row>
                    <Col>
                      Status
                    </Col>
                    <Col>
                      <strong>  {product.countInStock >0 ? (<span className="badge badge-success">In Stock</span>) : (<span className="badge badge-danger">Out of Stock</span>)} </strong>
                    </Col>
                  </Row>
                </ListGroup.Item>

                {product.countInStock > 0 && (
                  <ListGroup.Item>
                    <Row>
                      <Col>
                        Qty
                      </Col>
                      <Col>
                        <Form.Control as='select' value={qty} onChange={(e) => setQty(e.target.value)}>
                          {
                            [...Array(product.countInStock).keys()].map((x) => (
                              <option key={x+1} value={x+1}>{x+1}</option>
                            ))
                          }
                        </Form.Control>
                      </Col>
                    </Row>
                  </ListGroup.Item>
                )}

                <ListGroup.Item>
                  <Button onClick={addToCartHandler} className="btn btn-dark btn-block" type="button" disabled={product.countInStock ===0}>Add To Cart</Button>
                </ListGroup.Item>
              </ListGroup>
            </Card>
            
          </Col>
        </Row>
        <Row>
          <Col md={6}>
            <br/><h4 className="font-weight-bold text-uppercase">Reviews</h4>

            {product.reviews.length === 0 && <Message> No Reviews</Message>}            

            {/* This will print the comment with star */}
            <ListGroup variant='flush' className="px-0 my-2">
              
              {product.reviews.map((review) => (

              <ListGroup.Item key={review._id} className="px-0">

                <strong>{review.name}</strong>
                <Rating value={review.rating} />
                <p>{review.createdAt.substring(0, 10)}</p>
                <p>{review.comment}</p>

              </ListGroup.Item>
              
              ))}

              <ListGroup.Item className="px-0 my-2">
                <h5 className="px-0 pb-2 font-weight-bold text-dark">Write a Customer Review</h5>
                 
                 {errorProductReview && <Message variant='danger'> {errorProductReview}</Message>}  
                {userInfo ? (
                  <Form onSubmit={submitHandler}>
                   
                    <Form.Group controlId='rating'>
                      <Form.Label>Rating</Form.Label>
                      <Form.Control as='select' value={rating} onChange={(e) => setRating(e.target.value)}>
                      <option value=''>  Select...      </option>
                      <option value='1'> 1 - Poor      </option>
                      <option value='2'> 2 - Fair      </option>
                      <option value='3'> 3 - Good      </option>
                      <option value='4'> 4 - very Good </option>
                      <option value='5'> 5 - Excellent </option>
                      </Form.Control>
                    </Form.Group>

                    <Form.Group controlId='comment'>
                      <Form.Label>Comment</Form.Label>
                      <Form.Control as='textarea' row='3' value={comment} onChange={(e) => setComment(e.target.value)}>
                      </Form.Control>
                    </Form.Group>

                    <Button type='submit' variant="dark" disabled={loadingProductReview}> Submit </Button>
                  </Form>
                ) : <Message>Please <Link to='/login'> sign in </Link> to write a review </Message>}
              </ListGroup.Item>

            </ListGroup>
          </Col>
        </Row>
        </>
      )}
    </>
  )
}

export default ProductScreen
