import React, { useEffect } from 'react';
import { Link } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import { Row, Col, ListGroup, Image, Form, Button, Card} from 'react-bootstrap';
import Message from '../components/Message';
import { addToCart, removeFromCart } from '../actions/cartActions'

const CartScreen = ({ match, location, history }) => {
  const productId = match.params.id;

  const qty = location.search ? Number(location.search.split('=')[1]) : 1;
  console.log(qty);

  const dispatch = useDispatch();

  const cart = useSelector(state => state.cart);
  const { cartItems } = cart

  useEffect(() => {
    if(productId) {
      dispatch(addToCart(productId, qty))
    }
  },[dispatch, productId, qty])

  const removeFromCartHandler  = (id) => {
    dispatch(removeFromCart(id));
  }

  const checkoutHandler = () => {
    history.push('/login?redirect=shipping');
  }

  return (
    <Row>
      <Col md={8}>
        <h4 className="p-2 bg-light text-dark rounded">Shopping Cart</h4><br/>
        {cartItems.length === 0 ? (
          <Message>
            Your Cart is empty <Link to='/' className="pl-2 btn btn-light my-3"> <i className="fas fa-chevron-left pr-2"></i> Go Back </Link>
          </Message>
        ) : (  
          <ListGroup variant='flush'>
            {cartItems.map(item => (
              <ListGroup.Item key={item.product}>
                  <Row>
                    <Col md={2}>
                        <Image src={item.image} alt={item.name} fluid rounded />
                    </Col>
                    <Col md={3}>
                      <Link to={`/product/${item.product}`}>{item.name}</Link>
                    </Col>
                    <Col md={2}>
                       ${item.price}
                    </Col>
                    <Col md={2}>
                       <Form.Control as='select' value={item.qty} onChange={(e) => dispatch(addToCart(item.product, Number(e.target.value)))}>
                          {
                            [...Array(item.countInStock).keys()].map((x) => (
                              <option key={x+1} value={x+1}>{x+1}</option>
                            ))
                          }
                        </Form.Control>
                    </Col>
                    <Col md={2}>
                       <Button type='button' variant='light' onClick={() => removeFromCartHandler(item.product)}>
                        <i className="fas fa-trash "></i>
                       </Button>
                    </Col>
                  </Row>
              </ListGroup.Item>
            ))}
          </ListGroup>
        )}
      </Col>
      <Col md={4}>
            <Card>
              <ListGroup variant='flush'>
                <ListGroup.Item>
                  <h2>SubTotal ({cartItems.reduce((acc, item) => acc + item.qty, 0 ) }) Items</h2>
                  ${cartItems.reduce((acc, item) => acc + item.qty * item.price, 0).toFixed(2)}
                </ListGroup.Item>
                <ListGroup.Item>
                  <Button type="button" className="btn-block btn btn-dark" disabled={cartItems.length === 0} onClick={checkoutHandler}> Proceed To Checkout</Button>
                </ListGroup.Item>
              </ListGroup>
            </Card>              
      </Col>
    </Row>
  )
}

export default CartScreen
